@import url(https://fonts.googleapis.com/css?family=Lobster);
@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rowdies&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{--bg-one: #000000;--bg-two: #333e6e;--bg-three: #0e9ca7;--bg-panel: #fff;--color-headings: #fff;--color-text: #333;--color-fun: #c44394}body{color:#fff;color:var(--color-headings);background:radial-gradient(black 15%, transparent 16%) 0 0,radial-gradient(black 15%, transparent 16%) 8px 8px,radial-gradient(rgba(255,255,255,0.1) 15%, transparent 20%) 0 1px,radial-gradient(rgba(255,255,255,0.1) 15%, transparent 20%) 8px 9px;background-color:#282828;background-size:16px 16px;font-family:'Muli', sans-serif;min-height:100vh;display:grid;place-items:center;text-align:center}h1{font-size:75px;margin:10px;font-family:'Rowdies', sans-serif;text-shadow:2px 2px 3px rgba(0,0,0,0.3)}img.logo{width:400px;position:absolute;margin-left:-200px;margin-top:-110px}.countdown-div{width:580px;margin:auto;height:80vh}@media screen and (max-width: 500px){.countdown-div{width:inherit}.countdown-div .countdown-wrapper{padding-top:80px}}.countdown-wrapper{margin-top:200px;display:flex;min-height:200px;align-items:center;justify-content:center;flex-wrap:wrap;background:linear-gradient(315deg, white 0%, #a4a5a5 74%);border-radius:10px;box-shadow:4px 4px 12px 2px rgba(0,0,0,0.3);padding:40px 40px 30px}.countdown-item{color:#333;color:var(--color-text);font-size:4rem;display:flex;align-items:center;justify-content:center;flex-direction:column;line-height:30px;margin:10px;padding-top:10px;position:relative;width:100px;height:100px}@media screen and (max-width: 1366px){.countdown-item{font-size:3rem}}.countdown-item span{color:#333;color:var(--color-text);font-size:1rem;padding-top:1rem;text-transform:uppercase}.countdown-svg{position:absolute;top:0;left:0;width:100px;height:100px}.arrow{margin-top:120px}

h3.vertical-timeline-element-title{text-transform:uppercase;margin:8px 0}h4.vertical-timeline-element-subtitle{margin:0;color:white;background:#3d3d3d;border-radius:25px;padding:4px 18px}h4.vertical-timeline-element-subtitle a{color:white;text-decoration:none}.event-logo{width:inherit}h1 a.schedule-link,h3 a.schedule-link{color:white;text-decoration:none}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer {
  margin-top: 40px;
  margin-bottom: 20px;
}

.footer .developer {
  color: white;
}

